.Ship {
    overflow: hidden;
    z-index: 10;
}

.Ship img {
    height: 100%;
    width: 100%;
    display: block;
}

.horizontal {

}

.vertical {
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.N {
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.S {
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.W {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}


